import React from 'react';
import Paragraph from '../paragraph/paragraph.component';
import icone1 from '../../images/Emoticon/icons8-chevron-left-18 (1).png';
type BoxsType = {
    title?: string;
    src?: string;
    paragraph?: string;
    bigImg?: string;
    img1?: string;
    img2?: string;
    img3?: string;
    img4?: string;
    img5?: string;
    img6?: string;
    bottomTitle?:string;
}
const Boxs = (props: BoxsType) => {
    const smallImg = [
        { src: props.img1 },
        { src: props.img2 },
        { src: props.img3 },
    ]
    const smallImg2 = [
        { src: props.img4 },
        { src: props.img5 },
        { src: props.img6 },
    ]
    return (
        <div className='bg-white rounded-lg p-4'>
            <div className='flex items-center'>
                <img src={props.src} alt="" />
                <Paragraph className='text-black text-lg mr-2 mt-2' title={props.title} />
            </div>
            <Paragraph className='p-boxs text-sm mt-2' title={props.paragraph} />
            <div className='w-1/6 h-0.5 mt-3 mb-3 bg-backgrund'></div>
            <div className='flex items-center gap-2'>
                <div className='w-full'>
                <img src={props.bigImg} alt="" />
                </div>
                <div>
                    <div className='flex'>
                        {smallImg.map(item => {
                            return (
                                <div className='border-r p-4'>
                                    <img src={item.src} alt="" />
                                </div>
                            )
                        })}
                    </div>
                    <div className='flex'>
                        {smallImg2.map(item => {
                            return (
                                <div className='border-r border-t p-4'>
                                    <img src={item.src} alt="" />
                                </div>
                            )
                        })}
                    </div>
                </div>
            </div>
            <div className='flex mt-6 items-center justify-center'>
            <Paragraph className='text-backgrund text-base ml-1' title={props.bottomTitle} />
            <img className='h-4' src={icone1} alt="" />
            </div>
        </div>
    );
};

export default Boxs;