import React from 'react';
import icone1 from '../../images/Emoticon/filmlogo.svg';
import icone2 from '../../images/Emoticon/icons8-chevron-left-18white.png';
import Paragraph from '../paragraph/paragraph.component';
const Row4 = () => {
    return (
        <div className='max-w-7xl items-center mx-auto row4 gap-4 rounded-lg pl-4 pr-4 flex flex-col md:flex-row'>
            <div>
                <img src={icone1} alt="" />
                <Paragraph textAlignment='text-center' className='text-white text-lg mt-2' title='سرویس اشتراکی فیدیبو' />
            </div>
            <div className='w-px bg-white h-8'></div>
            <div>
            <Paragraph textAlignment='text-center' className='text-white text-base mt-2' title='با اشتراک در دیجی‌پلاس، از فیدیبو هم اشتراک رایگان می‌گیرید و به هزاران کتاب دسترسی پیدا می‌کنید.' />
            </div>
            <div className='row4-img'></div>
            <button className='text-white flex pt-2 pb-2 w-36 text-sm items-center border border-white rounded-full  justify-center'>
                اطلاعات بیشتر
                <img className='h4' src={icone2} alt="" />
                </button>
        </div>
    );
};

export default Row4;