import React from 'react';

type ButtonType = {
  title?: string;
  className?: string;
  src?: string;
}
const Button = (props: ButtonType) => {
  const className = props.className + ' ' + 'flex justify-center text-sm border items-center'
  return (
    <button className={className}>
      {props.src && <img src={props.src} alt="button-icone h-7" />}
      <span className='mr-2 mt-1'>{props.title}</span>
    </button>
  );
};

export default Button;