import React from 'react';
import Slider from "react-slick";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import icone1 from '../../images/Emoticon/images1.jpg';
import icone2 from '../../images/Emoticon/images2.jpg';
import icone3 from '../../images/Emoticon/images3.jpg';
const MenyImages = () => {
    const images = [
        { src: icone1 },
        { src: icone2 },
        { src: icone3 },
    ]
    var settings = {
        arrows:false,
        autoplay:true,
        dots: true,
        infinite: true,
        speed: 500,
        slidesToShow: 1,
        slidesToScroll: 1
    };
    return (
        <div className='w-10/12 md:w-3/5  m-auto max-w-7xl mx-auto bg-white -mt-8 rounded-lg p-3 shadow-2xl'>
            <div>
            <Slider {...settings}>
              {images.map(item=>{
                  return (
                      <img className='rounded ' src={item.src} alt="" />
                  )
              })}
            </Slider>
            </div>
        </div>
    );
};

export default MenyImages;