import React from 'react';
import Paragraph from '../paragraph/paragraph.component';
import icone1 from '../../images/Emoticon/dece308d.svg';
import icone2 from '../../images/Emoticon/icons8-chevron-down-18.png';
import Button from '../button/button.component';
const Row1 = () => {
    return (
        <div className='max-w-7xl mx-auto mt-4 pb-16 row1'>
            <img className='m-auto' src={icone1} alt="" />
            <Paragraph textAlignment='text-center' className='text-white text-3xl mt-6' title='عضو دیجی‌پلاس شوید تا خدمات ویژه‌تر بگیرید!' />
            <Paragraph textAlignment='text-center' className='text-white text-lg mt-3' title='ارسال رایگان | ۳۰ روز ضمانت بازگشت کالا | هدیه نقدی و ارسال فوری برای کالا‌های برگزیده' />
            <Button title='عضو دیجی‌پلاس شوید!' className='pl-6 pr-6 h-11 bg-white m-auto text-base text-backgrund rounded-lg mt-6 text-white' />
            <img className='mt-6 m-auto' src={icone2} alt="" />
        </div>
    );
};

export default Row1;