import React from 'react';
import icone1 from '../../images/Emoticon/icons8-calendar-24.png';
import Button from '../button/button.component';
import Paragraph from '../paragraph/paragraph.component';
type SquareBoxsType = {
    title?:string;
    button?:string;
    price?:string;
    discount?:string;
    smallButton?:string;
}
const SquareBoxs = (props:SquareBoxsType) => {
    return (
        <div className='bg-white rounded-lg border pt-12 pb-12 pr-4 pl-4'>
              <div className='m-auto rounded-full bg-pink-100 h-10 w-10 flex shadow-2xl items-center justify-center'>
                <img src={icone1} alt="" />
            </div>
            <Paragraph textAlignment='text-center' className='text-title text-2xl mt-4' title={props.title} />
            <Button title={props.button} className='w-full m-auto text-lg text-white bg-backgrund rounded-lg p-3' />
            <div className='flex gap-3 justify-center mt-3 items-center'>
            <Button title={props.smallButton} className='text-white bg-red-500 mt-3 flex items-center justify-center h-7 pl-2 w-12 rounded-full text-lg' />
            <Paragraph  className='p-boxs text-xl mt-4' title={props.discount} />
            <Paragraph  className='text-title text-2xl mt-4' title={props.price} />
            </div>
        </div>
    );
};

export default SquareBoxs;