import React from 'react';
import icone1 from '../../images/Emoticon/digipluse.svg';
import icone2 from '../../images/Emoticon/digikala.svg';
import icone3 from '../../images/Emoticon/icons8-checked-24.png';
import icone4 from '../../images/Emoticon/icons8-cross-mark-24.png';
import icone5 from '../../images/Emoticon/digikala.svg';
import icone6 from '../../images/Emoticon/amazing.svg';
import icone7 from '../../images/Emoticon/gitft.svg';
import icone8 from '../../images/Emoticon/private.svg';
import icone9 from '../../images/Emoticon/back.svg';
import icone10 from '../../images/Emoticon/send.svg';
import icone11 from '../../images/Emoticon/pluse.svg';
import Paragraph from '../paragraph/paragraph.component';
const ComparisonBoxs = () => {
    const data = [
        { iconeright: icone3, title: '۴ سفارش با هر مبلغی', src: icone6, textAlignment: 'ارسال رایگان', titleleft: 'فقط سفارش بالای ۳۰۰ ‌هزار تومان', iconeleft: icone3 },
        { iconeright: icone3, title: '۳۰ روزه', src: icone7, textAlignment: 'ضمانت بازپس‌گرفتن کالا', titleleft: '۷ روزه', iconeleft: icone3 },
        { iconeright: icone3, title: '', src: icone8, textAlignment: 'خط تماس پشتیبانی ویژه', titleleft: '', iconeleft: icone4 },
        { iconeright: icone3, title: '', src: icone9, textAlignment: 'شگفت‌های اختصاصی برای کاربران دیجی‌پلاس', titleleft: '', iconeleft: icone4 },
        { iconeright: icone3, title: '', src: icone10, textAlignment: 'هدیه نقدی روی خریدهای منتخب', titleleft: '', iconeleft: icone4 },
        { iconeright: icone3, title: '', src: icone11, textAlignment: 'اشتراک فیدی‌پلاس', titleleft: '', iconeleft: icone4 },

    ]
    return (
        <div className='container pt-4 pb-4 pl-2 pr-2 mt-4 w-full md:w-8/12 mx-auto bg-white shadow-2xl rounded-lg'>
            <div className='grid grid-cols-3 gap-4'>
                <div>
                    <img width='105px' className='m-auto' src={icone1} alt="" />
                </div>
                <div></div>
                <div>
                    <img width='105px' className='m-auto' src={icone2} alt="" />
                </div>
            </div>
            {data.map(item => {
                return (
                    <>
                        <div className='w-full border-line mt-4 mb-4 shadow-2xl'></div>
                        <div className='grid grid-cols-3 place-items-center gap-4'>
                            <div>
                                <img className='m-auto' src={item.iconeright} alt="" />
                                <Paragraph textAlignment='text-center' className='mt-1 text-xs text-backgrund' title={item.title} />
                            </div>
                            <div>
                                <img className='m-auto' src={item.src} alt="" />
                                <Paragraph textAlignment='text-center' className='text-title text-xs' title={item.textAlignment} />
                            </div>
                            <div>
                                <img className='m-auto' src={item.iconeleft} alt="" />
                                <Paragraph textAlignment='text-center' className='text-red-500 mt-1 text-xs' title={item.titleleft} />
                            </div>
                        </div>
                    </>
                )
            })}
        </div>
    );
};

export default ComparisonBoxs;