import React from 'react';
import Button from '../button/button.component';
import Paragraph from '../paragraph/paragraph.component';
import icone1 from '../../images/Emoticon/truck-icone.svg';
import icone2 from '../../images/Emoticon/amazing.svg';
import icone3 from '../../images/Emoticon/gitft.svg';
import icone4 from '../../images/Emoticon/private.svg';
import icone5 from '../../images/Emoticon/back.svg';
import icone6 from '../../images/Emoticon/send.svg';
import icone7 from '../../images/Emoticon/pluse.svg';
import ImagesTitle from '../images-title/iamges-title.component';
const Row2 = () => {
    const buttons = [
        { title: 'ارسال رایگان', src:icone1},
        { title: 'شگفت‌انگیز اختصاصی', src:icone2},
        { title: 'هدیه نقدی', src:icone3},
        { title: 'ظرفیت اختصاصی', src:icone4},
        { title: 'بازگشت کالا تا ۳۰ روز', src:icone5},
        { title: 'ارسال فوری پلاس', src:icone6},
        { title: 'فیدی‌پلاس', src:icone7},
    ]
    return (
        <div className='mt-28 container mx-auto p-6'>
            <Paragraph textAlignment='text-center' className='text-title text-3xl' title='خدمات و مزایای دیجی‌پلاس' />
            <div className='flex flex-wrap justify-center gap-3 mt-8'>
                {buttons.map(item => {
                    return (
                        <Button title={item.title} className='p-5 rounded-lg text-title shadow-2xl bg-white' src={item.src} />
                    )
                })}
            </div>
            <ImagesTitle/>
        </div>
    );
};

export default Row2;