import React from 'react';
import icone2 from '../../images/Emoticon/digikala.svg';
import Paragraph from '../paragraph/paragraph.component';
import icone3 from '../../images/Emoticon/icons8-chevron-up-18.png';
import icone4 from '../../images/Emoticon/bottom-icone1.svg';
import icone5 from '../../images/Emoticon/bottom-icone2.svg';
import icone6 from '../../images/Emoticon/bottom-icone3.svg';
import icone7 from '../../images/Emoticon/bottom-icone4.svg';
import icone8 from '../../images/Emoticon/bottom-icone5.svg';
import icone9 from '../../images/Emoticon/icons8-instagram-logo-40.png';
import icone10 from '../../images/Emoticon/icons8-twitter-40.png';
import icone11 from '../../images/Emoticon/icons8-linkedin-40.png';
import icone12 from '../../images/Emoticon/icons8-aparat-40.png';
import icone13 from '../../images/Emoticon/icone-digi.svg';
import icone14 from '../../images/Emoticon/googleplay.svg';
import icone15 from '../../images/Emoticon/bazar.svg';
import icone17 from '../../images/Emoticon/apele.svg';
import icone18 from '../../images/Emoticon/apele.svg';
import Button from '../button/button.component';

const Row8 = () => {
    const data = [
        { title: 'امکان تحویل اکسپرس', src: icone4 },
        { title: 'امکان پرداخت در محل', src: icone5 },
        { title: '۷ روز هفته، ۲۴ ساعته', src: icone6 },
        { title: '۷ روز ضمانت بازگشت کالا', src: icone7 },
        { title: 'ضمانت اصل بودن کالا', src: icone8 },
    ]
    const footer = [
        { title: 'با دیجی‌کالا', p1: 'اتاق خبر دیجی‌کالا', p2: 'فروش در دیجی‌کالا"', p3: 'فروش های شغلی', p4: 'تماس با دیجی کالا', p5: 'درباره ی دیجی کالا' },
        { title: 'خدمات مشتریان', p1: 'پاسخ به پرسش‌های متداول', p3: 'رویه‌های بازگرداندن کالا', p4: 'شرایط استفاده', p5: 'حریم خصوصی', p6: 'گزارش باگ' },
        { title: 'راهنمای خرید از دیجی‌کالا', p1: 'نحوه ثبت سفارش', p2: 'رویه ارسال سفارش', p3: 'شیوه‌های پرداخت' },
    ]
    const buttons = [
        { src: icone14 },
        { src: icone15 },
        { src: icone14 },
        { src: icone17 },
    ]
    const lastImage = [
        { src: icone18 },
        { src: icone18 },
        { src: icone18 },
        { src: icone18 },
        { src: icone18 },
        { src: icone18 },
        { src: icone18 },
        { src: icone18 },
    ]
    return (
        <div className='max-w-7xl mx-auto pl-6 pr-6 pt-14'>
            <div className='flex flex-col gap-4 justify-between md:flex-row'>
                <div>
                    <img width='105px' src={icone2} alt="" />
                    <div className='flex gap-4 mt-4 items-center'>
                        <Paragraph className='p-boxs text-sm' title='تلفن پشتیبانی:۶۱۹۳۰۰۰۰ - ۰۲۱' />
                        <div className='w-px bg-title h-3'></div>
                        <Paragraph className='p-boxs text-sm' title='هفت روز هفته، ۲۴ ساعت شبانه‌روز پاسخگوی شما هستیم.' />
                    </div>
                </div>
                <button className='border flex gap-4 p-boxs text-lg rounded-lg justify-center items-center w-40 h-10 m-auto md:m-0'>
                    بازگشت به بالا
                    <img src={icone3} alt="" />
                </button>
            </div>
            <div className='flex justify-between mt-8 flex-wrap'>
                {data.map(item => {
                    return (
                        <div>
                            <img width='40' className='m-auto' src={item.src} alt="" />
                            <Paragraph className='p-boxs text-sm mt-5' title={item.title} />
                        </div>
                    )
                })}
                <div className='w-full h-px bg-gray-200 mt-5 mb-5'></div>
                <div className='w-full flex justify-center flex-col md:flex-row md:justify-between'>
                    {footer.map(item => {
                        return (
                            <div>
                                <Paragraph className='text-title text-xl mt-4' title={item.title} />
                                <Paragraph className='p-boxs text-base mt-4' title={item.p1} />
                                <Paragraph className='p-boxs text-base mt-4' title={item.p2} />
                                <Paragraph className='p-boxs text-base mt-4' title={item.p3} />
                                {item.p4 && <Paragraph className='p-boxs text-base mt-4' title={item.p4} />}
                                {item.p5 && <Paragraph className='p-boxs text-base mt-4' title={item.p5} />}
                                {item.p6 && <Paragraph className='p-boxs text-base mt-4' title={item.p6} />}
                            </div>
                        )
                    })}
                    <div className='md:w-1/3'>
                        <Paragraph className='text-title text-xl mt-4' title='با ما همراه باشید' />
                        <div className='flex gap-4 mt-4'>
                            <img className='h-7' src={icone9} alt="" />
                            <img className='h-7' src={icone10} alt="" />
                            <img className='h-7' src={icone11} alt="" />
                            <img className='h-7' src={icone12} alt="" />
                        </div>
                        <Paragraph className='text-title text-xl mt-12' title='از جدیدترین تخفیف‌ها باخبر شوید' />
                        <div className='flex gap-2 mt-4'>
                            <input type="text" className='bg-input  rounded-lg pr-2 w-full h-11' placeholder='آدرس ایمیل خود را وارد کنید' />
                            <Button title='ثبت' className='pl-3 pr-3 rounded-lg text-white bg-gray-200 text-lg' />
                        </div>
                    </div>
                </div>
            </div>
            <div className='bg-bottom-boxs p-4 flex-col mt-8 flex rounded-lg justify-between md:flex-row'>
                <div className='flex items-center'>
                    <img src={icone13} alt="" />
                    <Paragraph className='text-white text-xl mr-4' title='دانلود اپلیکیشن ' />
                </div>
                <div className='flex gap-4 flex-wrap'>
                    {buttons.map(item => {
                        return (
                            <img src={item.src} alt="" />
                        )
                    })}
                    <div className='p-5 bg-white rounded-md'></div>
                </div>
            </div>
            <div className='flex flex-col gap-8 items-center justify-between md:flex-row'>
                <div>
                    <Paragraph className='text-title text-lg' title='فروشگاه اینترنتی دیجی‌کالا، بررسی، انتخاب و خرید آنلاین' />
                    <Paragraph className='p-boxs text-sm mt-4' title='دیجی‌کالا به عنوان یکی از قدیمی‌ترین فروشگاه های اینترنتی با بیش از یک دهه تجربه، با پایبندی به سه اصل، پرداخت در محل، ۷ روز ضمانت بازگشت کالا و تضمین اصل‌بودن کالا موفق شده تا همگام با فروشگاه‌های معتبر جهان، به بزرگ‌ترین فروشگاه اینترنتی ایران تبدیل شود. به محض ورود به سایت دیجی‌کالا با دنیایی از کالا رو به رو می‌شوید! هر آنچه که نیاز دارید و به ذهن شما خطور می‌کند در اینجا پیدا خواهید کرد.' />
                </div>
                <div className='flex gap-4 mt-12'>
                    <div className='p-16 border rounded-md'></div>
                    <div className='p-16 border rounded-md'></div>
                    <div className='p-16 border rounded-md'></div>
                </div>
            </div>
            <div className='w-full h-px bg-gray-200 mt-12 mb-5'></div>
            <Paragraph textAlignment='text-center' className='p-boxs mt-4' title='
        استفاده از مطالب فروشگاه اینترنتی دیجی‌کالا فقط برای مقاصد غیرتجاری و با ذکر منبع
        بلامانع است. کلیه حقوق این سایت متعلق به شرکت نوآوران فن آوازه (فروشگاه آنلاین دیجی‌کالا) می‌باشد.
    ' />
            {/* <div className='bg-gray-200 p-4 flex'>
                {lastImage.map(item => {
                    return (
                        <div className='flex items-center justify-center p-8 border-r border-b'>
                         <img src={item.src} alt="" />
                        </div>
                    )
                })}
            </div> */}
        </div>
    );
};

export default Row8;