import React from 'react';
import icone1 from '../../images/Emoticon/icons8-compare-24.png';
import icone2 from '../../images/Emoticon/icons8-info-18.png';
import Paragraph from '../paragraph/paragraph.component';
import SquareBoxs from '../Squareboxs/Squareboxs';
const Row6 = () => {
    return (
        <div className='container mx-auto row6 p-4 md:p-0'>
            <div className='m-auto rounded-full bg-pink-100 mt-24 h-12 w-12 flex shadow-2xl items-center justify-center'>
                <img src={icone1} alt="" />
            </div>
            <Paragraph textAlignment='text-center' className='text-title text-3xl mt-4' title='عضو دیجی‌پلاس شوید!' />
            <div className='grid grid-cols-1 gap-4 md:grid-cols-3 mt-80'>
                <SquareBoxs title='اشتراک ۱ ماهه' price='۴۵,۹۰۰ تومان' discount='۵۰,۰۰۰' button='خرید اشتراک ۱ ماهه' smallButton='۸٪' />
                <SquareBoxs title='اشتراک ۳ ماهه' price='۸۲,۵۰۰ تومان' discount='۱۵۰,۰۰۰' button='خرید اشتراک ۳ ماهه' smallButton='۴۵٪' />
                <SquareBoxs title='اشتراک ۶ ماهه' price='۱۲۸,۵۰۰ تومان' discount='۳۰۰,۰۰۰' button='خرید اشتراک ۶ ماهه' smallButton='۵۷٪' />
            </div>
            <div className='flex justify-center mt-7 items-center'>
                <img className='h-4 mt-4 ml-1' src={icone2} alt="" />
                <Paragraph textAlignment='text-center' className='p-boxs text-sm mt-4' title='به مبالغ فوق ۹% بابت مالیات بر ارزش افزوده اضافه می شود' />
            </div>
        </div>
    );
};

export default Row6;