import React from 'react';
import icone1 from '../../images/Emoticon/icons8-compare-24.png';
import ComparisonBoxs from '../Comparison-boxs/Comparison.boxs.component';
import Paragraph from '../paragraph/paragraph.component';
const Row5 = () => {
    return (
        <div className='max-w-7xl mx-auto pl-4 pr-4 md:pl-0 md:pr-0 pt-14'>
            <div className='m-auto rounded-full bg-white h-12 w-12 flex shadow-2xl items-center justify-center'>
                <img src={icone1} alt="" />
            </div>
            <Paragraph textAlignment='text-center' className='text-title text-3xl mt-4' title='مقایسه در یک نگاه' />
            <ComparisonBoxs/>
        </div>
    );
};

export default Row5;