import React from 'react';
import Slider from "react-slick";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import icone1 from '../../images/Emoticon/3-7.jpg';
import icone2 from '../../images/Emoticon/3-7.jpg';
import icone3 from '../../images/Emoticon/3-7.jpg';
import Paragraph from '../paragraph/paragraph.component';
const ImagesTitle = () => {
    const images = [
        { src: icone1, title: 'بخشی از مبلغ خرید، به حسا‌بتان برمی‌گردد' ,paragraph:'با دیجی‌پلاس، بر روی بیش از ۵۰،۰۰۰ کالا، امکان دریافت هدیه نقدی دارید. در صورت خرید کالاهای نشان‌دار، این هدیه نقدی بعد از نهایی شدن پروسه خرید (پایان ۳۰ روز مهلت بازگرداندن کالا) به کیف پول دیجی‌کالای شما واریز می‌شود.'},
        { src: icone2, title: 'در اولویت قرارمی‌گیرید',paragraph:'با دیجی‌پلاس، ظرفیت های تکمیل شده‌ی ارسال را برای شما به طور اختصاصی باز نگه می‌داریم. علاوه بر این در مرکز تماس، خط تماس پشتیبانی ویژه در اختیارتان خواهیم گذاشت.' },
        { src: icone3, title: 'از همیشه سریع‌تر تحویل می‌گیرید',paragraph:'اگر در سبد خرید خود کالایی داشته باشید که شامل گزینه‌ی "ارسال فوری دیجی‌پلاس" شود، با پرداخت هزینه‌ی ارسال، بین ۲-۴ ساعت بعد آن را تحویل می‌گیرید.' },
    ]
    var settings = {
        arrows: false,
        autoplay: true,
        dots: true,
        infinite: true,
        speed: 500,
        slidesToShow: 1,
        slidesToScroll: 1
    };
    return (
            <div className='mt-24 mb-24'>
                <Slider {...settings}>
                    {images.map(item => {
                        return (
                            <div className='images-title grid-cols-1 md:grid-cols-2 ' >
                                <div>
                                    <img  className='rounded m-auto' src={item.src} alt="" />
                                </div>
                                <div>
                                <Paragraph  className='text-title text-2xl' title={item.title} />
                                <div className='w-1/6 h-0.5 mt-3 mb-3 bg-backgrund'></div>
                                <Paragraph className='text-title text-lg' title={item.paragraph} />

                                </div>
                            </div>
                        )
                    })}
                </Slider>

            </div>
    );
};

export default ImagesTitle;