import React from 'react';
import icone1 from '../../images/Emoticon/logo.svg';
import Button from '../button/button.component';
import icone2 from '../../images/Emoticon/icons8-user-24.png';
import icone3 from '../../images/Emoticon/icons8-shopping-cart-24.png';
import icone4 from '../../images/Emoticon/icons8-search-24.png';
import icone5 from '../../images/Emoticon/icons8-sorting-18.png';
import icone6 from '../../images/Emoticon/icons8-ingredients-for-cooking-18.png';
import icone7 from '../../images/Emoticon/icons8-percentage-12.png';
import icone8 from '../../images/Emoticon/icons8-add-bookmark-16.png';
import icone9 from '../../images/Emoticon/icons8-star-18.png';
import icone10 from '../../images/Emoticon/icons8-gacha-club-18.png';
import icone11 from '../../images/Emoticon/icons8-pay-18.png';
import icone12 from '../../images/Emoticon/icons8-location-18.png';
import Paragraph from '../paragraph/paragraph.component';
const Menu = () => {
    const menu = [
        { title: 'دسته‌بندی کالاها', icone: icone5 },
        { title: 'سوپرمارکت', icone: icone6 },
        { title: 'تخفیف‌ها و پیشنهادها', icone: icone7 },
        { title: 'دیجی‌کالای من', icone: icone8 },
        { title: ' دیجی‌پلاس', icone: icone9 },
        { title: 'دیجی‌کلاب', icone: icone10 },
        { title: 'دیجی‌پی', icone: icone11 },
    ]
    const menu1 = [
        { title: ' دیجی‌پلاس' },
        { title: 'مزایا' },
        { title: 'خرید با دیجی‌پلاس' },
        { title: 'مقایسه دیجی‌پلاس و دیجی‌کالا' },
        { title: ' عضو شوید!' },
        { title: 'پرسش‌های شما' },
    ]
    return (
        <div className='max-w-7xl mx-auto'>
            <div className='grid grid-cols-1 justify-items-stretch pt-3 pb-3 pl-6 pr-6 md:grid-cols-2'>
                <div className='flex mb-3'>
                    <img height='30px' className='mt-1' src={icone1} alt="logo" />
                    <div className='relative mr-6 w-full'>
                        <input placeholder='جستجو در دیجی‌کالا …' type="text" className='bg-input text-sm rounded-lg pr-11 w-full h-11' />
                        <img className='absolute bottom-3 right-2' src={icone4} alt="" />
                    </div>
                </div>
                <div className='flex justify-center md:justify-end'>
                    <Button title='ورود به حساب کاربری' className='p-3 rounded-lg h-11 p-boxs' src={icone2} />
                    <div className='w-px h-6 mt-3 bg-title mr-4'></div>
                    <img className='mr-4 mt-3 h-7' src={icone3} alt="" />
                </div>
            </div>
            <div className='flex flex-col justify-between mb-3 pl-6 pr-6 md:flex-row'>
                <div className='flex flex-wrap gap-6 mb-3 md:mb-0'>
                    {menu.map(item => {
                        return (
                            <div className='flex gap-1 items-center'>
                                <img className='h-4' src={item.icone} alt="icone" />
                                <Paragraph className='color-maun-menu text-base' title={item.title} />
                            </div>
                        )
                    })}
                </div>
                <div className='flex items-center'>
                    <Paragraph className='color-maun-menu text-sm ml-2' title='لطفا شهر و استان خود را انتخاب کنید' />
                    <img className='h-4' src={icone12} alt="icone" />
                </div>
            </div>
            <div className='w-full h-px bg-line-menu mb-3'></div>
            <div className='flex pl-6 pr-6 gap-6 flex-col md:flex-row'>
                <div>
                    <img className='mt-1' src={icone1} alt="logo" />
                </div>
                <div className='flex items-center flex-wrap gap-6'>
                    {menu1.map(item => {
                        return (
                            <Paragraph className='p-boxs text-sm' title={item.title} />
                        )
                    })}
                </div>
            </div>
        </div>
    );
};

export default Menu;